import * as THREE from 'three';
import * as CANNON from 'cannon-es';

import { threeToCannon, ShapeType } from 'three-to-cannon';

import { FBXLoader } from 'three/examples/jsm/loaders/FBXLoader.js';

const PUBLIC_URL = `${process.env.PUBLIC_URL}/assets`;
export class Environment{
    
    private loader:FBXLoader;
    private _mixer:THREE.AnimationMixer;

    constructor(){
        this.loader = new FBXLoader();
    }
    
    Update(timeInSeconds) {
        if (this._mixer) {
            this._mixer.update(timeInSeconds);
          }
    }

    load(scene: THREE.Scene, world:CANNON.World){
        const scope=this;
        scope.loader.load(`${PUBLIC_URL}/models/landscape/landscape.fbx`, (fbx) => { 
            fbx.scale.setScalar(1);
            fbx.position.y = -23.4;


            scope._mixer = new THREE.AnimationMixer(fbx);
            fbx.animations.forEach(clip=>{
              const action = scope._mixer.clipAction(clip);
              action.play();
            });

            fbx.traverse(c => {
              c.castShadow = true;
              c.receiveShadow = true;
              if(c.name == "bridge_01" || c.name == "ground_07" || c.name == "labyrinth_01"){
                const result = threeToCannon((c as any), {type: ShapeType.MESH});
                const sphereBody= new CANNON.Body();
                sphereBody.addShape(result.shape);
                sphereBody.quaternion.set(c.quaternion.x,c.quaternion.y,c.quaternion.z, c.quaternion.w)
                sphereBody.position.set(c.position.x, c.position.y-23.4, c.position.z)
                world.addBody(sphereBody);
              }
            });
            scene.add(fbx);
        });
    }

}
